export default {
  name: "builder",
  props: {
    AssetData: "",
    assetadd: "", //数据
    formSubmit: "", //上传表单数据
    filedataName: "", //上传文件名字
    imageUrl: "", //默认显示的图片
    assetId: "", //修改资产id
    imamgeName: '' //点重置后传入的图片名称,手动让其回到原始状态
  },
  data() {
    return {
      imgName: '未选择任何图片', //图片名称
      show3: false,
      picked: "",
      datetime4: "2017-05-11",
      show: true,
      options: ["asb", "ddd"],
      grouping: [],
      // showfile:"",
      //scroll: "0", //缓存滚动值
      target: '',
      groupedData: [],
      saomiaoData: "", //扫码需要赋值的name
      validNumber: function(value) {
        var reg = /^(?:[1-9]\d*|0)(?:\.\d+)?$|^(?:\.0|0\.)$/;
        return {
          valid: reg.test(value),
          msg: "请输入数字"
        };
      }
    };
  },
  activated() {
    //清除图片名称
    this.imgName = '未选择任何图片';
    document.getElementById("setscroll").scrollTop = this.scroll;
  },
  watch: {
    pokerHistory: function(a, b) {
      if (a != "") {
        this.$http
          .get(
            "/API/assetservice/RefreshPageField",
            { params: { categoryId: a } },
            { emulateJSON: true }
          )
          .then(res => {
            this.groupedData = res.data;
            getValidationData(res.data);
          });
        this.$http
          .get(
            "/API/assetservice/RefreshPageFieldGroup",
            { params: { categoryId: a } },
            { emulateJSON: true }
          )
          .then(res => {
            this.grouping = res.data;
          });
      } else {
        this.getdata();
      }
    }
  },
  computed: {
    pokerHistory() {
      return this.formSubmit.categoryId;
    }
  },
  methods: {
    saomiao(e) {
      this.saomiaoData = e;
      this.saoma(this.fuzhi);
    },
    fuzhi(e) {
      // console.log(e)
      this.formSubmit[this.saomiaoData] = e;
    },
    clickdel(name, id) {
      if (id == "") {
        for (var i = 0, filelength = submifile.length; i < filelength; i++) {
          if (submifile[i].name == name) {
            submifile.splice(i, 1);
            this.$emit("acceptfile", submifile);
            return;
          }
        }
      } else {
        this.$http
          .post(
            "/API/assetservice/DeleteFile",
            { "": id },
            { emulateJSON: true }
          )
          .then(res => {
            GetAssetDetail(id);
          });
      }
    },
    togolle(e) {
      $(e.target)
        .next()
        .toggle();
    },
    numberChange(value, e) {
      var reg = /^(?:[1-9]\d*|0)(?:\.\d+)?$|^(?:\.0|0\.)$/;
      //  console.log(value,e)
      if (!reg.test(value)) {
        this.formSubmit[e] = "";
      }
    },
    Intnumber(value, e) {
      var reg = /^\d+$/;
      if (!reg.test(value)) {
        this.$message({
          showClose: true,
          message: "请输入整数"
        });
        this.formSubmit[e] = "";
      }
    },
    imgchange(event) {
      //获取文件名,手动渲染
      var filePath = $('#imgFile').val();
      var arr=filePath.split("\\");
      this.imgName = arr[arr.length-1];
      var obj = event.target;
      if (obj.files[0] != undefined) {
        this.$emit("accept", obj);
      }
    },
    subPic() {
      this.$refs.subimg.click();
    },
    openfile() {
      this.$refs.file.click();
    },
    fileChange(event) {
      
      var obj = event.target;
      var fl = obj.files[0];
      if (fl != undefined) {
        submifile.push(fl);
        this.$emit("acceptfile", submifile);
      }

      //}
    },
    //弹入部门选择页面
    linkDepartment() {
      if (this.formSubmit.useUserId == "") {
        this.$router.push({ path: "/department" });
      }
    },
    Deselectpage() {
      for (let i = 0, v = this.$refs.c1; i < v.length; i++) {
        this.$refs.c1[i].hide();
      }
    },
    getdata() {
      this.groupedData = this.AssetData;
      this.$http
        .get("/API/assetservice/GetGroupByAssetId", { emulateJSON: true })
        .then(res => {
          this.grouping = res.data;
        });
    },
    setscroll(e) {
      let dom = e.target;
      this.scroll = dom.scrollTop;
    }
  },
  mounted() {
    //清楚图片名称
    this.imgName = '未选择任何图片';
    this.getdata();
    var that = this
    //获取被点击的input框
    $(document).on('click','input',function(){
        that.target = this
        setTimeout(function(){
            that.target.scrollIntoView(true);
        }, 100);
    })
    var winHeight = $(window).height();  //获取当前页面高度
    //监听键盘弹起和收起,手动让页面滚到当前input框
    $(window).resize(function () {
        setTimeout(function(){
            that.target.scrollIntoView(true);
        }, 100);
    })
  }
};



